*{
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: 'Poppins', sans-serif;
	text-decoration: none;
	list-style: none;
	scroll-behavior: smooth;
}
body{
	background: #2a2a2a;
	color: #fff;
}
.header{
    display: flex;
    justify-content: space-between;
    padding: 10px 30px;
}
header.sticky{
	background: #2a2a2a;
	border-bottom: 1px solid #ffffff1a;
	padding: 12px 15%;
}
.logo{
	color: #fff;
	font-size: 35px;
	font-weight: 700;
	letter-spacing: 1px;
}
span{
	color: #ff4d05;
}
.navlist{
    display: flex;
    padding-right: 30px;
    padding-top: 15px;
}
.link{
	color: #ccc;
	font-size: 17px;
	font-weight: 500;
	margin: 0 25px;
	transition: all .45s ease;
}
.link:hover{
	color: #ff4d05;
}
.link.active{
	color: #ff4d05;
}